p {
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
}

body {
  font-size: 1rem;
  font-family: "Bruno Ace", cursive;
}

.key {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 55px;
  height: 55px;
  border: 1px solid #000;
  box-shadow: 1px 1px 3px #000;
  transition: all 0.2s ease;
}
.key:hover {
  background: rgb(126, 219, 72);
  transition: all 0.2s ease;
}
.key:active {
  transform: translate(2px, 2px);
  background: rgb(55, 115, 20);
  color: aliceblue;
}
.key_first-row {
  width: 46px;
}
.key_second-row {
  width: 98px;
}
.key_therd-row {
  width: 128px;
}
.key_forth-row {
  width: 163px;
}
.key_space {
  width: 406px;
}
.key_back-space {
  width: 150px;
}
.key_right-size {
  width: 93px;
}

.arrow {
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-position: center;
}
.arrow__up {
  background-image: url("./../images/svg/sort-up-solid.svg");
  align-self: flex-end;
}
.arrow__down {
  background-image: url("./../images/svg/sort-down-solid.svg");
  align-self: flex-start;
}
.arrow__left {
  background-image: url("./../images/svg/caret-left-solid.svg");
}
.arrow__right {
  background-image: url("./../images/svg/caret-right-solid.svg");
}

.active {
  transform: translate(2px, 2px);
  background: rgb(55, 115, 20);
  color: aliceblue;
}

.arrow-mnemonik_up, .arrow-mnemonik_down, .arrow-mnemonik_left, .arrow-mnemonik_right {
  visibility: hidden;
  opacity: 0;
}

.content {
  font-size: 18px;
  text-align: center;
}

.wrapper {
  margin: 0 auto;
  padding: 24px;
}

.keyBoard__container {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  width: 936px;
  background: beige;
  margin: 0 auto;
}

.title {
  font-size: 2.4rem;
  text-align: center;
  margin: 0 auto;
  padding: 0 0 24px;
}

.input {
  font-size: 20px;
}
.input__container {
  width: 931px;
  display: block;
  margin: 0 auto 100px;
}
.input:focus {
  outline: 1px solid transparent;
}